.todo__Form {
	width: 100%;
	margin-bottom: 1rem;
}

.todo-input {
	outline: none;
	background: none;
	border: 1px solid #8758ff;
	padding: 0.5rem 1rem;
	margin-top: 1rem;
	margin-bottom: 2rem;
	width: 300px;
	color: #fff;
}

.todo-input::placeholder {
	color: #ffffff4d;
}

.todo-submit {
	background: #8758ff;
	color: #fff;
	border: none;
	padding: 0.55rem;
	cursor: pointer;
}
